/* Base styles */
/*Blue*/
/*Gray*/
/*Shadows*/
/*Breakpoints*/
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&family=Mulish:wght@300;400;500;600;700&display=swap");
*, *:before, *:after {
  box-sizing: border-box;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  font-family: sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

*:focus {
  outline: none;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: flex !important;
  height: 0;
  overflow: hidden;
}

a {
  color: inherit;
  text-decoration: none;
}

a:active,
a:hover {
  outline: 0;
}

ul, ol, p, h1, h2, h3, h4, h5, h6, figure {
  margin: 0;
  padding: 0;
}

ul[class], ol[class] {
  list-style-type: none;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button:focus,
input:focus,
optgroup:focus,
select:focus,
textarea:focus {
  outline: none;
}

button {
  padding: 0;
  overflow: visible;
  border: none;
  background: none;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  border-radius: 0;
  line-height: normal;
}

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

label {
  cursor: pointer;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td, th {
  padding: 0;
}

.clear-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.clearfix::after {
  clear: both;
  content: "";
  display: block;
}

::-moz-selection {
  background: #ffcc48;
  color: #181f23;
  /* Safari */
}

::selection {
  background: #ffcc48;
  color: #181f23;
  /* Safari */
}

::-moz-selection {
  background: #ffcc48;
  color: #181f23;
  /* Firefox */
}

