.label {
  margin-bottom: 8px;
  line-height: 24px;
  font-size: 18px;
  font-weight: 700;
}
@media (min-width: 768px) {
  .label.label-sum {
    margin-bottom: 0;
    line-height: 28px;
  }
}
@media (min-width: 1024px) {
  .label.label-sum {
    font-weight: 500;
  }
}

.label-info {
  padding-right: 4px;
  font-size: 12px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .label-info {
    font-size: 14px;
    font-weight: 600;
  }
}
@media (min-width: 1024px) {
  .label-info {
    line-height: 24px;
    font-size: 16px;
  }
}

.label-small {
  color: #6D6D6D;
  line-height: 12px;
  font-size: 10px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .label-small {
    font-size: 14px;
    line-height: 20px;
  }
}

.payment-header-label {
  color: #1C3F64;
  line-height: 16px;
  text-align: inherit;
  font-size: 12px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .payment-header-label {
    font-size: 14px;
    line-height: 20px;
  }
}

