body {
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 100vh;
  width: 100%;
  margin: 0;
  color: #1C3F64;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  font-weight: normal;
}

main {
  flex-grow: 1;
  padding-bottom: 80px;
}
@media (min-width: 1024px) {
  main {
    display: grid;
    grid-template-columns: 400px 1fr;
    grid-column-gap: 24px;
    grid-auto-rows: min-content;
  }
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 16px;
  padding-right: 16px;
}
@media (min-width: 768px) {
  .container {
    max-width: 578px;
  }
}
@media (min-width: 1024px) {
  .container {
    max-width: 1008px;
  }
}

.underline {
  position: relative;
}
.underline:after {
  content: "";
  background-color: #1C3F64;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  transform-origin: right;
  transition: transform 0.25s;
  width: 100%;
  transform: scaleX(0);
  pointer-events: none;
}

