.btn {
  border-radius: 8px;
  line-height: 1;
  font-size: 16px;
  font-weight: 600;
  transition-duration: 0.25s;
}
@media (min-width: 1024px) {
  .btn:hover {
    box-shadow: 0 3px 5px rgba(16, 24, 40, 0.15), 0 1px 2px rgba(16, 24, 40, 0.06);
  }
}
.btn:disabled {
  color: rgba(255, 255, 255, 0.5);
}

.btn-danger {
  padding: 8px 16px;
  background-color: #DD5368;
  color: #fff;
  font-weight: 400;
}
@media (max-width: 767px) {
  .btn-danger {
    font-size: 12px;
  }
}
@media (min-width: 1024px) {
  .btn-danger:hover {
    background-color: #EA828F;
    box-shadow: 0 3px 5px rgba(16, 24, 40, 0.07);
  }
}
.btn-danger:active {
  background-color: #C93553;
}
.btn-danger:disabled {
  background-color: #EA828F;
}

.btn-primary {
  padding: 14px 16px;
  color: #fff;
  background-color: #5DA7F5;
}
@media (min-width: 1024px) {
  .btn-primary:hover {
    background-color: #96C9FA;
  }
}
.btn-primary:active {
  background-color: #3D6B9C;
}

.btn-secondary {
  padding: 14px 16px;
  color: #fff;
  background-color: #B0B0B0;
}
@media (min-width: 1024px) {
  .btn-secondary:hover {
    background-color: #D1D1D1;
  }
}
.btn-secondary:active {
  background-color: #8C8C8C;
}
.btn-secondary:disabled {
  background-color: #D1D1D1;
  color: rgba(255, 255, 255, 0.5);
}

.btn-outline {
  display: inline-block;
  padding: 8px 16px;
  background-color: transparent;
  border-radius: 8px;
  border: 1px solid #5DA7F5;
  white-space: nowrap;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
  color: #1C3F64;
}
.btn-outline.active:after {
  transform: rotate(180deg);
}
@media (min-width: 1024px) {
  .btn-outline:hover {
    background-color: #EFF7FF;
    border-color: #EFF7FF;
  }
}
.btn-outline:active {
  background-color: #DCEBFD;
}
.btn-outline:disabled {
  background-color: #F6F6F6;
  color: #DAE6EB;
}
.btn-outline.btn-chat {
