/*Gray*/
/*Shadows*/
/*Breakpoints*/
.custom-radio input[type=radio] {
  display: none;
}
.custom-radio input[type=radio]:checked + label:before {
  background-color: #EFF7FF;
}
.custom-radio input[type=radio]:checked + label:after {
  background-color: #5DA7F5;
}
.custom-radio label {
  position: relative;
  display: flex;
  align-items: center;
  padding: 12px 8px;
  border: 1px solid #DCEBFD;
  border-radius: 8px;
  line-height: 20px;
  font-size: 14px;
  font-weight: 500;
}
@media (min-width: 768px) {
  .custom-radio label {
    padding-top: 8px;
    padding-bottom: 8px;
    font-size: 16px;
    line-height: 22px;
  }
}
.custom-radio label:before, .custom-radio label:after {
  content: "";
  border-radius: 100%;
  background-color: transparent;
  transition-duration: 0.25s;
  transition-property: background-color;
}
.custom-radio label:before {
  width: 16px;
  height: 16px;
  margin-right: 8px;
  border: 1px solid #DCEBFD;
}
.custom-radio label:after {
  position: absolute;
  left: 12px;
  top: 50%;
  width: 8px;
  height: 8px;
  transform: translateY(-50%);
}

.custom-radio-text input[type=radio] {
  display: none;
}
.custom-radio-text input[type=radio]:checked + label {
  color: #1C3F64;
}
.custom-radio-text input[type=radio]:checked + label:after {
  transform: scaleX(1);
  transform-origin: left;
}
.custom-radio-text label {
  display: block;
  position: relative;
  padding: 8px 0;
  line-height: 1;
  font-size: 14px;
  font-weight: 500;
  color: #8C8C8C;
  white-space: nowrap;
}

