.show-more .btn {
  width: 100%;
}
.show-more .collapsible {
  height: 0;
  opacity: 0;
  overflow: hidden;
  transition: all 0.4s;
  pointer-events: none;
}
.show-more .collapsible.is-open {
  margin-top: 8px;
  opacity: 1;
  pointer-events: auto;
}
.show-more .inner {
  padding: 8px;
  border: 1px solid #DCEBFD;
  border-radius: 8px;
  font-size: 14px;
  line-height: 18px;
}
.show-more .inner p:not(:last-child) {
  margin-bottom: 8px;
}

.input-copy {
  display: flex;
  height: 46px;
  margin-top: 2px;
  padding: 4px 16px;
  gap: 8px;
  align-items: center;
  border: 1px solid #DAE6EB;
  background-color: #F6F6F6;
  border-radius: 8px;
  font-size: 12px;
  line-height: 16px;
}
@media (min-width: 768px) {
  .input-copy {
    font-size: 14px;
    line-height: 20px;
  }
}
.input-copy input, .input-copy textarea {
  height: 100%;
  width: 100%;
  border-width: 0;
  background-color: transparent;
}
.input-copy textarea {
  resize: none;
